.chatItem{
    height: 60px;
    background: #e69e19;
    cursor: pointer;
    &_body{
        height: 100%;
        display: flex;
        align-items: center;
        padding: 0 15px;
        justify-content: space-between;
    }
    &-friendLogin{
        color: #b419e6;
    }
    &-delete{
        width: 25px;
        cursor: pointer;
    }
}